export default {
  data() {
    return {
      url:"",
      scrollTop: 0,
      loginShow2: false,
      loginShow3: false,
      model_text: "",
      model_password: "",
      wifi_text: "",
      wifi_password: "",
      child_text: "",
      child_number: ""
    };
  },
  onPageScroll(Object){
    console.log(Object);
  },
  methods: {
    scroll: function(e) {
				// console.log(e)
				this.scrollTop = e.mp.detail.scrollTop
			},
    //点击下一步 显示下一模块
    button_next: function(e, index) {

					// this.scrollTop = 820
		
      console.log(this.scrollTop);
      if (index == 1) {

        if (this.model_text.length>=6&&this.model_password.length>=6) {
          this.loginShow2 = true;
          console.log("model:" + this.model_text, this.model_password);
          let auth_timetimer = setInterval(() => {
            this.scrollTop = this.scrollTop+1; 
            if (this.scrollTop >= 430) {
				this.scrollTop = 430; 
              clearInterval(auth_timetimer);
           }
          },1);
        } else {
          uni.showToast({
              title:'账号密码不能低于六位',
			  icon: 'none',
              duration: 1000
                  })
        }
      }
      // 
      if (index == 2) {
        if (this.wifi_text.length>=6 && this.wifi_password >=6) {
          this.loginShow3 = true;
          let auth_timetimer = setInterval(() => {
            this.scrollTop = this.scrollTop+1; 
            if (this.scrollTop >= 840) {
				this.scrollTop = 840; 
              clearInterval(auth_timetimer);
           }
          }, 1);
          console.log("wifi:" + this.wifi_text, this.wifi_password);
        } else {
          uni.showToast({
              title: '账号密码不能低于六位',
			  icon: 'none',
              duration: 1000
                  })
        }
      }
      // 
      if (index == 3) {
        let that = this;
        if (this.child_text != "" && this.child_number != "") {
          uni.request({
            // url: "http://192.168.1.133/web/api/addDeviceUser",
             
              url: that.url+"web/api/addDeviceUser",
            header: {
              "content-type": "application/json"
            },
            data: { 
              mac: that.model_text ,
            remark:that.model_password,
            deviceUserInfo:that.wifi_text,
            deviceId:that.wifi_password,
             patriarch :that.child_text
            },
            method: "post",
            success: function(res) {
              console.log(res);
            },
            fail: function() {
              // fail
              console.log("request..----------->....fail");
            },
          });
        } else {
          uni.showToast({
              title:  '账号密码不能低于六位',
			  icon: 'none',
              duration: 1000
                  })
        }
      }
    }
    // qwe:function(){

    // }
  },
  onLoad:function(){
    this.url=this.$consturl;
  }
};
